:root {
  --btn-w: 6em;
  --btn-h: 2em;
  --btn-border-w: 1em;
  --size-large:  clamp(1rem, calc(2vw + 0.5rem), 2rem);
  --size-normal: clamp(0.75rem, calc(1vw + 0.5rem), 1.5rem);
  --size-small: clamp(0.75rem, calc(1vw + 0.3rem), 1rem);
  --post-btn-bg: rgb(187, 158, 255);
  --form-bg: rgb(209, 195, 255);
  --main-bg: linear-gradient(36deg, rgba(73,77,166,1) 0%, rgba(222,0,255,1) 100%) 0 0 / 100% no-repeat
}

::-webkit-scrollbar {
  width: 0px!important;
}

* {
  padding: 0;
  margin: 0;
  border: 0;
}

*, *:before, *:after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus, :active {
  outline: none;
}

a:focus, a:active {
  outline: none;
}

nav, footer, header, aside {
  display: block;
}

html, body {
  font-size: 100%;
  line-height: 1;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input, button, textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a, a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: 400;
}
html {
  position: relative;
}
html, body {
  min-height: 100%;
}
body {
  /*height: 100vh;*/
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(73,77,166);
  background: var(--main-bg);
}


.post-enter {
  opacity: 0;
  transform: translateX(-350px);
}
.post-enter-active {
  transform: translateX(0);
  opacity: 1;
  transition: all 500ms ease-in;
}
.post-exit {
  opacity: 1;
}
.post-exit-active {
  transform: translateX(350px);
  opacity: 0;
  transition: all 500ms ease-in;
}

.modal-enter {
  opacity: 0;
}
.modal-enter-active {
  opacity: 1;
  transition: opacity 300ms ease-in;
}
.modal-exit {
  opacity: 1;
}
.modal-exit-active {
  opacity: 0;
  transition: opacity 300ms ease-in;
}

